













import { Vue, Component, Prop, Watch } from 'vue-property-decorator'

@Component
export default class BiuCard extends Vue {
    @Prop(String) title?: string
    @Prop(Boolean) border?: boolean
    /**
     * attrsthis.$attrs
     * v-bind="$attrs"
     * data
     * $attrsattrs,v-bind="attrs"
     */
    private attrs = {}
    /**
     * $attrs
     */
    @Watch('$attrs', { immediate: true })
    $attrsChange(newVal: any) {
        if (
            JSON.stringify(Object.keys(newVal)) !==
                JSON.stringify(Object.keys(this.attrs)) ||
            JSON.stringify(newVal) !== JSON.stringify(this.attrs)
        )
            this.attrs = newVal
    }
}
