

























import {
    Vue,
    Component,
    Model,
    Watch,
    Emit,
    Prop
} from 'vue-property-decorator'
import { Select, Option } from 'element-ui'

@Component({
    components: {
        [Select.name]: Select,
        [Option.name]: Option
    }
})
export default class BiuSelect extends Vue {
    @Model('setValue') value!: string
    @Prop({ type: Boolean, default: false }) multiple?: boolean

    customValue: string | string[] = ''

    variable = true // 
    /**
     * attrsthis.$attrs
     * v-bind="$attrs"
     * data
     * $attrsattrs,v-bind="attrs"
     */
    private attrs = {}
    private listeners = {}

    @Watch('value', { immediate: true })
    valueChange(newVal: string | string[]) {
        // 
        if (newVal === undefined || newVal === null) {
            if (this.multiple) {
                this.customValue = []
            } else {
                this.customValue = ''
            }
        } else if (
            JSON.stringify(newVal) !== JSON.stringify(this.customValue)
        ) {
            if (this.multiple) {
                this.customValue = (newVal as string[]).map((item: any) =>
                    String(item)
                )
            } else {
                this.customValue = String(newVal)
            }
        }
    }
    @Watch('customValue')
    customValueChange() {
        this.setValue()
    }
    /**
     * $attrs
     */
    @Watch('$attrs', { immediate: true })
    $attrsChange(newVal: any) {
        if (
            JSON.stringify(Object.keys(newVal)) !==
                JSON.stringify(Object.keys(this.attrs)) ||
            JSON.stringify(newVal) !== JSON.stringify(this.attrs)
        )
            this.attrs = newVal
    }
    @Watch('$listeners', { immediate: true })
    $listenersChange(newVal: any) {
        if (
            JSON.stringify(Object.keys(newVal)) !==
            JSON.stringify(Object.keys(this.listeners))
        )
            this.listeners = newVal
    }
    @Emit('setValue')
    setValue() {
        return this.customValue
    }
    /**
     * 
     */
    removeTag(id: string) {
        const options = (this.attrs as any).options
        const disabled = options.filter((item: any) => item.value === id)[0]
            .disabled
        if (disabled) {
            // 
            this.variable = false
        }
    }
    /**
     * 
     */
    clear() {
        if (this.multiple) {
            // 
            this.variable = false
            const options = (this.attrs as any).options
            const disabledIds = options
                .filter((item: any) => item.disabled)
                .map((item: any) => item.value)
            this.customValue = (this.customValue as string[]).filter(
                (item: any) => disabledIds.includes(item)
            )
        }
    }
    /**
     * 
     */
    change(value: string | string[]) {
        //   
        // ,tag ,
        // ,tag
        setTimeout(() => {
            if (this.variable) this.customValue = value
            this.variable = true
        }, 0)
        // v-on
        // ;(this.listeners as any).change && (this.listeners as any).change(value)
    }
}
