




































import {
    Component,
    Emit,
    Vue,
    PropSync,
    Prop,
    Watch
} from 'vue-property-decorator'

@Component
export default class BiuDialog extends Vue {
    @PropSync('visible') visibleSync!: boolean
    @Prop() private btnLoading?: boolean
    /**
     * attrsthis.$attrs
     * v-bind="$attrs"
     * data
     * $attrsattrs,v-bind="attrs"
     */
    private attrs = {}
    /**
     * $attrs
     */
    @Watch('$attrs', { immediate: true })
    $attrsChange(newVal: any) {
        if (
            JSON.stringify(Object.keys(newVal)) !==
                JSON.stringify(Object.keys(this.attrs)) ||
            JSON.stringify(newVal) !== JSON.stringify(this.attrs)
        )
            this.attrs = newVal
    }

    @Emit()
    cancel() {
        this.visibleSync = false
    }
}
